*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  -webkit-tap-highlight-color: transparent;
  line-height: 2;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.site {
  height: 100%;
}

img {
  max-width: 100%;
  height: auto;
}

.site-notice {
  width: 100%;
  float: left;
  color: #edebec;
}

.site-notice--warning {
  background-color: #b74a48;
}

.site-notice--success {
  background-color: #26b27b;
}

@media screen and (max-width: 39.9375em) {
  iframe {
    width: 100%;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: normal;
  line-height: 1.2;
}

h1,
.heading-1 {
  font-size: 2rem;
}

h2,
.heading-2 {
  font-size: 1.75rem;
}

h3,
.heading-3 {
  font-size: 1.5rem;
}

h4,
.heading-4 {
  font-size: 1.5rem;
}

h5,
.heading-5 {
  font-size: 1.5rem;
}

h6,
.heading-6 {
  font-size: 1.5rem;
}

a,
input[type="submit"],
input[type="button"],
button {
  cursor: pointer;
}

ul {
  margin: 0;
}

.btn {
  margin: 0;
  border: 0;
  display: inline-block;
  text-decoration: none;
  line-height: normal;
}

.btn-primary {
  background-color: #000000;
  color: #edebec;
  padding: 0.3125rem;
}

.btn-primary:hover {
  background-color: #333333;
}

.page-banner {
  width: 100%;
  height: 15.625rem;
}

.icon {
  width: 1.5625rem;
  height: 1.5625rem;
  fill: black;
}

.site-header {
  width: 100%;
  float: left;
}

.site-header::after, .site-header::before {
  content: ' ';
  display: table;
  flex-basis: 0;
  order: 1;
}

.site-header::after {
  clear: both;
}

.site-header__top, .site-header__bottom {
  max-width: 1165px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.site-header__top::after, .site-header__top::before, .site-header__bottom::after, .site-header__bottom::before {
  content: ' ';
  display: table;
  flex-basis: 0;
  order: 1;
}

.site-header__top::after, .site-header__bottom::after {
  clear: both;
}

.site-logo {
  font-size: 2rem;
}

.site-logo a {
  text-decoration: none;
  color: #000000;
}

.site-nav {
  width: 100%;
}

.site-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.site-footer {
  width: 100%;
  float: left;
}

.site-footer__main {
  padding-top: 1.875rem;
  padding-bottom: 1.875rem;
}

.site-footer__bottom {
  background-color: #1a1a1a;
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
}

.site-footer__col {
  width: 50%;
}

.breadcrumbs {
  width: 100%;
}

.breadcrumbs__item {
  display: inline-block;
}

.breadcrumbs__item:not(:first-of-type):before {
  content: '\2044';
  padding-left: 0.625rem;
  padding-right: 0.625rem;
}
